@use "helper"as help;


#info_container {
    width: 100%;
    height: 100vh;
    @include help.flex(row);
    align-items: center;
    justify-content: center;

    img {
        width: 40rem;
        height: 25rem;
        margin-left: 8%;
    }

    #info__text_box {
        position: relative;
        top: 5rem;
        left: -8rem;
        width: 35rem;
        height: auto;
        padding: 2% 4%;
        font-family: help.$ff1;
        background-color: help.$White;

        #info__heading {
            font-family: help.$ff2;
            font-weight: 500;
            font-size: 3rem;
            text-transform: uppercase;
        }

        #info__paragraph {
            padding: 2% 0;
            font-size: 1rem;
            color: help.$DarkGray;
        }


    }

}

@media screen and (max-width:600px) {
    #info_container {
        @include help.flex(column);
        align-items: center;

        img {
            width: 80%;
            height: 40%;
            margin-left: 0;
            margin: 2% 0;
        }

        #info__text_box {
            top: 0;
            left: 0;
            width: 90%;
            margin: 2% 0;

            #info__heading {
                text-align: center;
                font-size: 2.2rem;
                padding: 2% 0;
            }

            #info__paragraph {
                text-align: center;
            }

        }

    }
}