@use "helper"as help;

// section 2
#section_header {
    width: 100%;
    @include help.flex(row);
    justify-content: space-between;
    align-items: center;
    padding: 2% 7%;
    font-family: help.$ff2;
    color: help.$VeryDarkGray;

    #section__heading {
        text-transform: uppercase;
        font-size: 2rem;
    }

    #section__button {
        outline: none;
        border: 2px solid help.$DarkGray;
        background-color: transparent;
        font-size: 1rem;
        padding: 1% 6%;
        transition: border 1s ease;

        &:hover {
            border: 2px solid blue;

        }
    }
}

// gird images
#grid_image_container {
    width: 100%;
    @include help.grid(repeat(4, 1fr), repeat(2, 1fr));
    grid-gap: 1rem;
    padding: 2% 5%;
    font-family: help.$ff2;
    margin-bottom: 5%;

    .grid__item {
        @include help.flex(column);
        justify-content: flex-end;
        width: 15rem;
        height: 25rem;

        .grid__text {
            width: 70%;
            font-size: 2rem;
            color: help.$White;
            padding: 10%;
            z-index: 20;
        }
    }

    #item1 {
        background-image: url('../images/desktop/image-deep-earth.jpg');
    }

    #item2 {
        background-image: url('../images/desktop/image-night-arcade.jpg');
    }

    #item3 {
        background-image: url('../images/desktop/image-soccer-team.jpg');
    }

    #item4 {
        background-image: url('../images/desktop/image-grid.jpg');
    }

    #item5 {
        background-image: url('../images/desktop/image-from-above.jpg');
    }

    #item6 {
        background-image: url('../images/desktop/image-pocket-borealis.jpg');
    }

    #item7 {
        background-image: url('../images/desktop/image-curiosity.jpg');
    }

    #item8 {
        background-image: url('../images/desktop/image-fisheye.jpg');
    }
}


@media screen and (max-width:600px) {

    // section 2
    #section_header {
        justify-content: center;
        padding: 2% 7%;

        #section__heading {
            font-size: 2.5rem;
        }

        #section__button {
            display: none;
        }
    }

    // gird images
    #grid_image_container {
        @include help.grid(1fr, repeat(8, 1fr));
        padding: 2% 5%;

        .grid__item {
            justify-content: flex-end;
            width: 100%;
            height: 8rem;
            background-position: center;
            background-size: cover;

            .grid__text {
                width: 50%;
                padding: 5%;
                font-size: 1.7rem;
            }
        }

        #item1 {
            background-image: url('../images/mobile/image-deep-earth.jpg');
        }

        #item2 {
            background-image: url('../images/mobile/image-night-arcade.jpg');
        }

        #item3 {
            background-image: url('../images/mobile/image-soccer-team.jpg');
        }

        #item4 {
            background-image: url('../images/mobile/image-grid.jpg');
        }

        #item5 {
            background-image: url('../images/mobile/image-from-above.jpg');
        }

        #item6 {
            background-image: url('../images/mobile/image-pocket-borealis.jpg');
        }

        #item7 {
            background-image: url('../images/mobile/image-curiosity.jpg');
        }

        #item8 {
            background-image: url('../images/mobile/image-fisheye.jpg');
        }
    }
}