@use "helper"as help;
// footer

footer {
    width: 100%;
    @include help.grid(1fr, 1fr 1fr 0.1fr);
    place-items: flex-start center;
    background-color: help.$Black;

    .footer__row {
        width: 100%;
        @include help.flex(row);
        justify-content: space-between;
        padding: 1% 5%;
        margin: 1% 0;


        #footer__icons {
            width: 20%;

            img {
                margin: 0 2%;
            }
        }

        #footer__list {
            @include help.flex(row);
            justify-content: flex-start;
            font-family: help.$ff2;

            .nav__links {
                @include help.links;
                font-size: 1.2rem;
                font-weight: 600;
                margin: 0 2%;
            }
        }

        #footer__info {
            color: help.$DarkGray;
            font-size: 0.9rem;
            font-family: help.$ff1;
        }
    }

    .attribution {
        width: 100%;
        text-align: center;
        color: help.$DarkGray;
        font-size: 0.9rem;
        font-family: help.$ff1;
        padding: 1%;
    }

}

@media screen and (max-width:600px) {
    footer {
        @include help.flex(column);

        .footer__row {
            width: 100%;
            @include help.flex(column);
            justify-content: center;
            align-items: center;
            padding: 1% 5%;
            margin: 1% 0;

            img {
                margin: 4% 0;
            }

            #footer__icons {
                margin: 2% 0;
                width: 100%;
                text-align: center;
            }

            #footer__list {
                @include help.flex(column);
                justify-content: center;

                .nav__links {
                    font-size: 1.2rem;
                    margin: 5% 0;
                    padding: 5% 0;
                }
            }

            #footer__info {
                margin: 2% 0;
                font-size: 1rem;
            }
        }

        .attribution {
            font-size: 1rem;
            padding: 3%;
        }

    }
}