@use "helper"as help;



// header
header {
    width: 100%;
    height: 100vh;
    background-image: url('../images/desktop/image-hero.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    @include help.grid(1fr, 0.3fr 1fr);
    place-items: flex-start;
    color: help.$White;

    .hidden {
        display: none;
    }

    nav {
        width: 100%;
        @include help.grid(1fr 2fr, auto);
        padding: 5%;

        .hidden {
            display: none;
        }

        #nav__logo {
            font-family: help.$ff1;
            font-size: 1.6rem;
            font-weight: 600;
        }

        #nav__list {
            width: 100%;
            @include help.flex(row);
            justify-content: flex-end;
            font-family: help.$ff2;
            color: help.$DarkGray;

            .nav__links {
                @include help.links;
                font-size: 1.2rem;
                font-weight: 600;
                margin: 0 2%;
                transition: all 1s ease;

                &:hover {
                    color: #fff;
                    padding: 0 2%;
                    font-weight: 900;
                }
            }
        }
    }

    #header_text_box {
        position: relative;
        top: 10%;
        left: 10%;
        width: 40%;
        padding: 2% 4%;
        border: 2px solid help.$White;

        p {
            text-transform: uppercase;
            font-family: help.$ff2;
            font-weight: lighter;
            font-size: 3.5rem;
        }
    }
}

@media screen and (max-width:600px) {
    header {
        background-position: center;
        background-size: cover;
        @include help.grid(1fr, 0.2fr 1fr);

        nav {
            @include help.grid(1fr 1fr, auto);
            place-items: center;
            padding: 2%;

            .hidden {
                display: flex;
            }

            #nav__menu_logo {
                @include help.flex(row);
                justify-content: flex-end;
                align-items: center;
                width: 100%;
                padding: 5%;

                img {
                    width: 2rem;
                }
            }

            #nav__list {
                @include help.flex(column);
                text-align: left;
                margin-right: 0%;
                display: none;

                .nav__links {
                    font-size: 1.5rem;
                }
            }
        }

        #header_text_box {
            top: 0%;
            left: 0%;
            width: 90%;
            padding: 5% auto;
            text-align: left;

            p {
                font-size: 3rem;
            }
        }
    }
}