@use "helper"as help;

.hidden {
    display: none;
}

// mobile nav
#mobile_nav {
    width: 100%;
    height: 100vh;
    background-color: black;
    @include help.grid(1fr, 1fr 2fr);
    place-items: center;
    position: fixed;
    z-index: 30;
    transform: translateX(-30rem);
    transition: all 1s ease;

    #nav__head {
        width: 100%;
        @include help.flex(row);
        justify-content: space-between;
        margin: 2% 0;
        padding: 4%;

        img {
            margin: 0 2%;
            cursor: pointer;
        }
    }

    #nav__list {
        width: 100%;
        height: 100%;
        @include help.flex(column);
        // justify-content: flex-start;
        margin-top: -10%;

        a {
            @include help.links();
            width: 100%;
            padding: 2% 4%;
            margin: 5% 0;
            font-size: 2rem;
            color: #c2b9b9;
            text-transform: uppercase;
            transition: color 1s ease;

            &:hover {
                color: #fff;
            }
        }

    }
}



@media screen and (min-width:900px) {
    #mobile_nav {
        display: none;
    }
}